.wrapper {
  position: relative;

  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
  overflow: hidden;


  font-family: $ff-lato;
  font-weight: 400;
  font-size: $fz-default;
  line-height: normal;

  .header,
  .footer {
    flex-shrink: 0;
  }

  main {
    flex-grow: 1;
    background-image: url("../img/order/order-background.png");
    background-repeat: no-repeat;
    background-position-x: center;
    background-position-y: center;
    background-color: $color-jaguar;
    width: 100%;
    background-size: contain;

    @include vp-1023 {
      background-size: cover;
    }
  }
}

.rouble {
  // font-family: $ff-rouble;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;

  white-space: nowrap;

  border: 0;

  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;

  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}
